import { useSelector } from 'react-redux';
var useNodes = function (nodesType) {
    var nodes = useSelector(function (state) {
        if (nodesType === 'intervieweeNodes') {
            return state.nodesState.intervieweeNodes.nodes;
        }
        return state.nodesState.interviewerNodes.nodes;
    });
    return nodes;
};
export { useNodes };
