import { useSelector } from 'react-redux';
var useOrder = function (nodesType) {
    return useSelector(function (state) {
        if (nodesType === 'intervieweeNodes') {
            return state.nodesState.intervieweeNodes.orders;
        }
        return state.nodesState.interviewerNodes.orders;
    });
};
export { useOrder };
